// Header
//
// A simple header containing just the logo.
//
// Styleguide header
@mixin header($color-scheme: blue-on-white) {
  @include section-margin;
  width: auto !important;

  .logo {
    @include text-style(logo);

    .link {
      @include link($color-scheme, $color-scheme, $color-scheme, $color-scheme);
      background-color: transparent !important;

      .flip__span {
        background-color: transparent !important;

        &:before {
          background-color: transparent !important;
        }
      }
    }

    .flip {
      @include flip;
    }
  }

  @include landscape {
    position: fixed;
    bottom: $lem * 2;
  }
}
